@import './valiables.scss';

#landing {
    width: 100%;
    height: 100vh;
    position: fixed;
    text-align: center;
    background-color: $Back0;
    #logo {
        padding: 120px 0;
        animation: fadein 2s;
        -webkit-animation: fadein 2s;
    }
}

#login {
    width: 100%;
    height: 100vh;
    position: fixed;
    text-align: center;
    background-color: $Back0;
    * {
        animation: fadein 2s;
        -webkit-animation: fadein 2s;
    }
    #logo {
        margin: auto;
        margin-top: 50px;
        display: block;
    }
    #id, #pw {
        width: 200px;
        height: 28px;
        display: block;
        margin: 5px auto;
    }
    button {
        width: 214px;
        height: 40px;
        display: block;
        margin: 5px auto;
        background-color: $Back2;
    }
    #login_btn {
        margin-top: 30px;
    }
}

#find_pw {
    margin-top: 150px;
    text-align: center;
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    .input {
        width: 200px;
        height: 28px;
        display: block;
        margin: 10px auto;
    }
    button {
        width: 214px;
        height: 40px;
        margin: 20px auto;
    }
}


#find_pw_res {
    text-align: center;
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    p {
        font-weight: bold;
        margin: 0 auto;
        margin-top: 180px;
        margin-bottom: 60px;
    }
    button {
        width: 240px;
        height: 40px;
    }
}

#join {
    text-align: center;
    margin-top: 90px;
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    .input {
        width: 200px;
        height: 23px;
        margin: 5px auto;
    }
    #gender_input {
        margin: 10px auto;
        label {
            margin-left: 6px;
        }
    }
    .inputSelect {
        width: 214px;
        height: 38px;
        display: block;
        margin: 0 auto;
    }
    button {
        width: 214px;
        height: 40px;
        margin-top: 30px;
    }
}