@import './valiables.scss';

#home {
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    #search_book {
        padding-bottom: 30px;
        border-bottom: 1px solid $tableBorder;
        text-align: center;
        .title {
            margin: 10px auto;
            font-weight: bold;
            text-align: left;
        }
        #search_input {
            width: 90%;
            height: 25px;
        }
        #search_button {
            width: 27px;
            top: 117px !important;
            right: 40px;
            margin: 0 auto;
            position: absolute;
            float: right;
            color: $Back2;
        }
    }

    #recommended_book {
        padding-top: 20px;
        overflow-x: scroll;
        .title {
            margin: 10px auto;
            position: absolute;
            font-weight: bold;
            text-align: left;
        }
        #list {
            height: 250px;
            width: 800px;
            margin-top: 40px;
            .list_item {
                width: 150px;
                padding: 5px;
                margin: 0 auto;
                float: left;
                text-align: center;
                .img-container {
                    width: 100%;
                    height: 160px;
                    #book_img {
                        height: 100%;
                        object-fit: fill;
                        border: 1px solid $BorderGray;
                    }
                }
                #book_name {
                    font-size: $x-small;
                }
                #author {
                    font-size: $xx-small;
                }
            }
        }
    }
}

#search {
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    margin-bottom: 20px;
    text-align: center;
    #search_book {
        margin-bottom: 20px;
        #search_input {
            width: 90%;
            height: 25px;
        }
        #search_button {
            width: 27px;
            top: 66px;
            right: 40px;
            margin: 0 auto;
            position: absolute;
            float: right;
            color: $Back2
        }
    }
    #search_result {
        .item_wrap {
            border: 1px solid $BorderGray;
            border-radius: 10px;
            padding: 8px;
            margin-bottom: 20px;
            text-align: center;
            box-shadow: 2px 2px 2px $BorderGray;
            table {
                margin: 0 auto;
                #title {
                    font-weight: bold;
                }
            }
            button {
                width: 80%;
            }
        }
    }
}

#search_detail {
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    width: 80%;
    margin-bottom: 20px;
    text-align: center;
    #info {
        padding-bottom: 10px;
        border-bottom: 1px solid $BorderGray;
        border-collapse: separate;
        border-spacing: 0 10px;
        margin: 0 auto;
        #book_name {
            font-size: large;
            font-weight: bold;
        }
        #td_title {
            font-weight: bold;
            text-align: right;
        }
    }
    #location {
        border-collapse: separate;
        border-spacing: 0 10px;
        .title {
            font-size: large;
            font-weight: bold;
            text-align: left;
        }
        table {
            margin: 0 auto;
            #td_title {
                font-weight: bold;
                text-align: right;
                padding-right: 10px;
            }
            #button {
                padding: 3px 0px;
                color: $white;
                font-weight: bold;
                background-color: $Back1;
                border: 1px solid $Back2;
                border-radius: 5px;
            }
        }
    }
}

#search_location {
    width: 90%;
    animation: fadein 2s;
    -webkit-animation: fadein 2s;
    margin: 100px auto 60px auto;
    #highlight {
        background-color: $Back3 !important;
    }
    #location1 {
        margin: 20px 0px;
        padding-bottom: 20px;
        border-bottom: 1px solid $BorderGray;
        .boxRow {
            width: 90%;
            height: 70px;
            margin: 0 auto;
            background-color: $Back1;
            text-align: center;
            .box {
                width: 17px;
                height: 50px;
                margin: 10px 8px;
                display: inline-block;
                position: relative;
                background-color: $white-;
            }
        }
        #door {
            width: 30%;
            height: 30px;
            line-height: 30px;
            margin: 5px auto;
            text-align: center;
            font-weight: bold;
            color: $white;
            background-color: $Back1;
        }
    }  
    #location2 {
        #bookshelf {
            width: 120px;
            height: 180px;
            padding: 5px;
            background-color: $Back1;
            .shelf {
                width: 100px;
                height: 30px;
                margin: 10px auto;
                background-color: $white-;
            }
        }
        #book_info {
            width: 50%;
            float: right;
            top: -180px;
            right: 0px;
            position: relative;
            #td_title {
                font-weight: bold;
                padding-bottom: 10px;
            }
        }
    }
}